li {
    list-style: none;
}
.title  {
    text-align: center;
    font-weight: normal;
    color: #515b64;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.statistics {
    height: 130px;
    width: 270px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 3px 2px 10px 1px #1f3349;
    border-radius: 5px;
    margin-top: 50px;

}
.stat_list {
    display: flex;
   
    padding: 0px;
 justify-content: center;
}
.item {
    display: flex;
    flex-direction: column;
    width: 35px;
    height: 35px;
    justify-content: space-around;
} 
.label {
    font-size: 10px;
}
.percentage {
    font-size: 14px;
}
span {
    padding: 0;
   
}